.nav1bar {
    position: fixed;
   
    background: #030452;
    transition: 0.2s ease;
    
    top: 0;
    height: 100vh;
    z-index:100;
    opacity:0.2;
    
  }
  .nav1bar:hover{
      opacity:1;
  }
  .nav1bar-nav1 {
    list-style: none;
    margin: 1rem;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .nav1-item {
    width: 100%;
    
  }
  
 
  .nav1-link {
    display: flex;
   
    text-decoration: none;
    color: white;
    height: auto;
  }
  
  .nav1-link:hover {
    color: orange;
    justify-content: center;
    
  }
  
  .link-text {
    display: none;
    margin: 1rem;
    font-weight: 600;
    align-self: center;
  }
  
  .nav1-link svg {
    width: 3rem;
    margin: 0rem 1rem;
    filter: grayscale(0.7);
   
  }
  
  .nav1-link:hover svg {
    filter: grayscale(0);
  }
  .nav1bar:hover {
    width: 30rem;
  }
  
  .nav1bar:hover .link-text {
   display: inline-block;
   transition: 0.2s ease;
   align-items: center;
  }
  #drop-head{
      display:none;
  }
  #drop-head:target{
      display:block;
  }

  
  /* large devices */
  
  @media (min-width:1000px) {
    .nav1bar {
      display:none;
       
    }
  
   
  }
  
